*{
  box-sizing: border-box;
  margin: 0%;
  padding: 0%;
  font-family: Arial, Helvetica, sans-serif;
}
.main_div{
  background: blueviolet;
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;


}
.center_div{
  background-color: aqua;
  height: 80vh;
  width: 25%;
  box-shadow: 5px 5px 25px -5px rgba(0, 0, 0,0.5);
  border-radius: 15px;
}

h1{
  color: white;
  background: transparent;
  background-color: brown;
  padding: 6PX 0 2px 0;
  margin-bottom: 10px;
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
}

input{
  text-align: center;
  height: 30px;
  top: 10px;
  border: none;
  background: transparent;
  font-size: 20px;
  font-weight: 500;
  width: 60%;
  border-bottom: 2px solid #8566aa;
  outline: none;
}

button {
  min-height: 40px;
  width: 40px;
  border-radius: 50%;
  border-color: transparent;
  background-color: #8566aa;
  color: #fff;
  font-size: 40px;
  border: none;
  outline: none;
  margin-left: 10px;
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);

}
button:hover{
  background-color: #20bf6b;
}
 ol {
  margin-top: 30px;
 }
 ol li {
  padding-left: 0px;
  text-align: left;
  font-size: 20px;
  font-weight: 500;
  min-height: 40px;
  display: flex;
  align-items: center;
  color: #8566aa;
  text-transform: capitalize;
 }

 .todo_style{
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
}

.todo_style .fa-circle-xmark {
  width: 20px;
  height: 20px;
  display:flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  border-radius: 50px;
  margin: 0 15px 0 35px;
  color: black;
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);


}

.todo_style .fa-xmark:hover{
  background-color: red;
}